import React from 'react'
import { motion } from 'framer-motion'
import './Contact.css'

const Contact = () => {
  return (
    <section className="contact">
      <div className="container">
        <motion.div 
          className="contact-content"
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
        >
          <div className="contact-links">
            <motion.a 
              href="https://linkedin.com/in/zirui-wen-76320b327/" 
              className="contact-link" 
              target="_blank" 
              rel="noopener noreferrer"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              LinkedIn ↗
            </motion.a>
            <motion.a 
              href="https://github.com/wzrdl" 
              className="contact-link" 
              target="_blank" 
              rel="noopener noreferrer"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              GitHub ↗
            </motion.a>
          </div>
          <div className="contact-info">
            <motion.a 
              href="mailto:wzrqczj@gmail.com" 
              className="contact-email"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              wzrqczj@gmail.com
            </motion.a>
            <p className="contact-phone">+1 201-932-5000</p>
          </div>
        </motion.div>
      </div>
    </section>
  )
}

export default Contact
