import React from 'react'
import { motion } from 'framer-motion'
import './WorkExperience.css'

const WorkExperience = () => {
  const experiences = [
    {
      id: 1,
      title: "Research Assistant — Brain Imaging & Graph Learning Lab",
      meta: "Nov 2024 – Present. Stevens Institute of Technology",
      icon: "/icon/Stevens-Apparel-Color-R.png",
      highlights: [
        "Architected GraphRAG pipeline integrating Neo4j (Cypher) with FAISS dense retrieval via LangChain. EM +32% on neurology FAQ benchmark",
        "Fine-tuned LLaMA-3 & Mistral-7B with LoRA, exported 8-bit quantized artifacts. Achieved +20% F1 on brain-science Q&A",
        "Built custom RL environment and trained PPO for multi-step seizure-trajectory prediction. +17% higher cumulative reward vs. baselines"
      ]
    },
    {
      id: 2,
      title: "Data Team Assistant Intern — Siemens",
      meta: "Nov 2023 – March 2024. Hoboken, NJ",
      icon: "/icon/ee59a5b2025bded085056f6cda27aca7.png",
      highlights: [
        "Built modular Python pipelines (Pandas/NumPy) to automate multi-source data ingestion, reducing manual processing time by 60%",
        "Analyzed 2+ years of transactional data using SQL and Excel to compute KPIs and evaluate A/B tests, improving engagement by 12%"
      ]
    }
  ]

  return (
    <section className="work-experience">
      <div className="container">
        <div className="section-layout">
          <div className="section-header">
            <h2 className="section-title">
              Work Experience<span className="accent-dot">.</span>
            </h2>
            <p className="section-description">
              I have been a part of the high quality design process for several companies.
            </p>
          </div>
          <div className="experience-list">
            {experiences.map((experience, index) => (
              <motion.div
                key={experience.id}
                className="experience-item"
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: index * 0.1 }}
                viewport={{ once: true }}
              >
                <div className="experience-icon">
                  <img src={experience.icon} alt="Company Logo" />
                </div>
                <div className="experience-content">
                  <h3 className="experience-title">{experience.title}</h3>
                  <p className="experience-meta">{experience.meta}</p>
                  <ul className="experience-highlights">
                    {experience.highlights.map((highlight, idx) => (
                      <li key={idx}>{highlight}</li>
                    ))}
                  </ul>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}

export default WorkExperience
