import React, { useState, useRef, useEffect, useCallback } from 'react'
import { motion } from 'framer-motion'
import './SampleWork.css'

const SampleWork = () => {
  const [activeIndex, setActiveIndex] = useState(-1)
  
  const titleRefs = useRef([])

  // Project data
  const projects = [
    {
      id: 0,
      title: "Real-time recommendation microservice",
      description: "I built a high-performance recommendation service using Java Spring Boot (WebFlux) with Apache Kafka for real-time streaming and Redis for low-latency features. The system is fully containerized with Docker/Kubernetes, featuring HPA autoscaling and comprehensive monitoring via Micrometer→Prometheus→Grafana. This microservice architecture enables real-time personalized recommendations at scale.",
      image: "/image/2021-05-27 221024.jpg",
      link: "https://github.com/wzrdl/recommendation-service"
    },
    {
      id: 1,
      title: "High-performance B+Tree database engine",
      description: "I designed and implemented a complete mini-RDBMS in C++ featuring an order-3 B+Tree storage engine with buffer pool management, secondary indexes, and write-ahead logging (WAL). The system includes an interactive SQL shell supporting 15 SQL commands with O(log n) query latency, demonstrating deep understanding of database internals and efficient data structures.",
      image: "/image/2021-05-27 221037.jpg",
      link: "https://github.com/wzrdl/bplustree-database"
    },
    {
      id: 2,
      title: "GPU-accelerated kernel K-means clustering",
      description: "I refactored kernel K-means algorithms to leverage sparse linear algebra operations (SpMM/SpMV) and optimized for GPU execution using CUDA and cuSPARSE libraries. Through careful memory throughput optimization and occupancy tuning, I achieved remarkable performance gains: 1000× faster than CPU and 2.6× faster than dense GPU implementations on MNIST and CIFAR-10 datasets.",
      image: "/image/2021-06-04 184906.jpg",
      link: "https://github.com/wzrdl/kernel-kmeans-cuda"
    },
    {
      id: 3,
      title: "AI-powered medical prompt optimization",
      description: "I developed an advanced prompt optimization system using text-gradient techniques with momentum and Bayesian reverse validation. The system significantly improved performance on medical AI benchmarks, achieving 20% better accuracy on MedQA/PubMedQA compared to Chain-of-Thought approaches. I integrated hybrid retrieval combining BM25 and dense embeddings with cross-encoder reranking for enhanced relevance.",
      image: "/image/2021-06-04 184906(3).jpg",
      link: "https://github.com/wzrdl/automatic-fullstack"
    }
  ]

  // 项目检测逻辑 - 当每个 sample 的标题顶部越过浏览器中心（位于上半区）才激活，若多个满足选距离中心最近的
  const handleScroll = useCallback(() => {

    // 简化：以浏览器可视区域为基准，不依赖额外 DOM 变量
    const viewportHeight = window.innerHeight
    const viewportCenter = viewportHeight / 2
    const viewportQuarter = viewportHeight * 0.25

    let bestMatch = null
    let minDistance = Infinity

    // 遍历所有标题
    titleRefs.current.forEach((ref, index) => {
      if (!ref) return

      const rect = ref.getBoundingClientRect()
      const titleTop = rect.top
      const titleHeight = rect.height
      const titleBottom = rect.bottom
      const titleCenter = titleTop + titleHeight / 2

      // 只考虑标题在可视区内
      if (titleTop < viewportHeight && titleBottom > 0) {
        // 条件：标题中心位于“上半区的 25%~50% 区间”
        if (titleCenter >= viewportQuarter && titleCenter <= viewportCenter) {
          const distance = viewportCenter - titleCenter
          if (distance < minDistance) {
            bestMatch = index
            minDistance = distance
          }
        }
      }
    })

    // 若存在候选，则切换到距离最近的上半区标题；否则若当前有激活则取消激活
    if (bestMatch !== null && bestMatch !== activeIndex) {
      setActiveIndex(bestMatch)
    } else if (bestMatch === null && activeIndex !== -1) {
      setActiveIndex(-1)
    }
  }, [activeIndex])

  // 使用自定义滚动检测
  useEffect(() => {
    window.addEventListener('scroll', handleScroll, { passive: true })
    return () => window.removeEventListener('scroll', handleScroll)
  }, [handleScroll])

  // Switch to specific project
  const switchToProject = (index) => {
    if (index === activeIndex) return
    setActiveIndex(index)
  }

  // 相册堆叠位置 - 模拟真实的专辑堆叠效果
  const getStackPosition = (index, isActive) => {
    const basePositions = [
      { x: -25, y: -25, rotation: -5, z: 1, scale: 0.9 },
      { x: -15, y: -15, rotation: -3, z: 2, scale: 0.93 },
      { x: -5, y: -5, rotation: -1, z: 3, scale: 0.96 },
      { x: 5, y: 5, rotation: 1, z: 4, scale: 0.96 }
    ]

    if (isActive) {
      // 被选中的专辑：从堆叠中抽取到顶层
      return {
        x: 0,
        y: -40,
        rotation: 0,
        scale: 1.2,
        z: 10
      }
    }

    return basePositions[index] || basePositions[0]
  }

  // 项目列表动画变体
  const projectItemVariants = {
    inactive: {
      opacity: 0.5,
      x: -20,
      scale: 0.95,
      transition: {
        type: "spring",
        stiffness: 300,
        damping: 30,
        duration: 0.5
      }
    },
    active: {
      opacity: 1,
      x: 0,
      scale: 1,
      transition: {
        type: "spring",
        stiffness: 300,
        damping: 30,
        duration: 0.5
      }
    },
    hover: {
      opacity: 0.8,
      x: 8,
      scale: 1.02,
      transition: {
        type: "spring",
        stiffness: 400,
        damping: 25,
        duration: 0.3
      }
    }
  }

  // 相册抽取动画变体
  const albumVariants = {
    initial: (position) => position,
    animate: (position, isActive) => {
      if (isActive) {
        // 抽取动画：从堆叠位置移动到顶层
        return {
          x: 0,
          y: -40,
          rotate: 0,
          scale: 1.2,
          zIndex: 10,
          transition: {
            type: "spring",
            stiffness: 200,
            damping: 25,
            duration: 0.8
          }
        }
      } else {
        // 返回堆叠位置
        return {
          ...position,
          transition: {
            type: "spring",
            stiffness: 200,
            damping: 25,
            duration: 0.8
          }
        }
      }
    },
    hover: (position, isActive) => {
      if (isActive) {
        // 已选中专辑的hover效果
        return {
          x: 0,
          y: -45,
          rotate: 0,
          scale: 1.25,
          zIndex: 10,
          transition: {
            type: "spring",
            stiffness: 400,
            damping: 20,
            duration: 0.3
          }
        }
      } else {
        // 未选中专辑的hover效果
        return {
          x: position.x - 8,
          y: position.y - 8,
          rotate: position.rotation - 2,
          scale: position.scale + 0.05,
          zIndex: position.z + 2,
          transition: {
            type: "spring",
            stiffness: 400,
            damping: 20,
            duration: 0.3
          }
        }
      }
    }
  }

  // 移除键盘激活与点击触发，仅保留滚动检测（渲染受 activeIndex 纯计算驱动）

  return (
    <section className="projects-vinyl">
      <div className="container">
        <div className="section-header">
          <h2 className="section-title">Sample work</h2>
        </div>
        
        <div className="vinyl-player-container">
          {/* Left Column: Project List */}
          <div className="vinyl-projects-list">
            {projects.map((project, index) => (
              <motion.div
                key={project.id}
                className={`project-item-vinyl ${activeIndex === index ? 'active' : ''}`}
                data-project={project.id}
                variants={projectItemVariants}
                initial="inactive"
                animate={activeIndex === index ? "active" : "inactive"}
                whileHover="hover"
              >
                <div className="project-content">
                  <motion.h3 
                    className="project-title"
                    ref={(el) => (titleRefs.current[index] = el)}
                    initial={{ opacity: 0.8 }}
                    animate={{ opacity: activeIndex === index ? 1 : 0.8 }}
                    transition={{ duration: 0.3 }}
                  >
                    {project.title}
                  </motion.h3>
                  <motion.p 
                    className="project-description"
                    initial={{ opacity: 0.7 }}
                    animate={{ opacity: activeIndex === index ? 1 : 0.7 }}
                    transition={{ duration: 0.3, delay: 0.1 }}
                  >
                    {project.description}
                  </motion.p>
                  <motion.div 
                    className="project-links"
                    initial={{ opacity: 0 }}
                    animate={{ opacity: activeIndex === index ? 1 : 0.6 }}
                    transition={{ duration: 0.3, delay: 0.2 }}
                  >
                    <a 
                      href={project.link} 
                      className="project-link" 
                      target="_blank" 
                      rel="noopener noreferrer"
                    >
                      View case study ↗
                    </a>
                  </motion.div>
                </div>
              </motion.div>
            ))}
          </div>

          {/* Right Column: Album Player Area */}
          <div className="album-player-area">
            <div className="album-player-container">
              <motion.div 
                className="album-stack"
                initial={{ opacity: 0, scale: 0.8 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ 
                  type: "spring", 
                  stiffness: 200, 
                  damping: 25,
                  duration: 0.8,
                  delay: 0.2
                }}
              >
                {projects.map((project, index) => {
                  const isActive = activeIndex === index
                  const position = getStackPosition(index, isActive)
                  
                  return (
                    <motion.div
                      key={project.id}
                      className={`album-item ${isActive ? 'active' : ''}`}
                      initial="initial"
                      animate="animate"
                      variants={albumVariants}
                      custom={[position, isActive]}
                      whileHover="hover"
                      style={{
                        zIndex: position.z
                      }}
                    >
                      <motion.img
                        src={project.image}
                        alt={project.title}
                        whileHover={{ 
                          scale: 1.05,
                          transition: { duration: 0.2 }
                        }}
                        style={{ 
                          borderRadius: "14px",
                          objectFit: "cover"
                        }}
                      />
                      {/* 动态阴影效果 */}
                    <motion.div
                        className="album-shadow"
                        initial={{ opacity: 0, scale: 0.8 }}
                        animate={{ 
                          opacity: isActive ? 0.4 : 0.15,
                          scale: isActive ? 1.3 : 1,
                          y: isActive ? 10 : 0
                        }}
                        transition={{ 
                          duration: 0.5,
                          type: "spring",
                          stiffness: 200,
                          damping: 25
                        }}
                      />
                    </motion.div>
                  )
                })}
              </motion.div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default SampleWork
