import React from 'react'
import { motion } from 'framer-motion'
import './Publications.css'

const Publications = () => {
  const publications = [
    {
      id: 1,
      category: "Press",
      title: "Zirui Wen + Stevens Win 2025 Research Excellence Award",
      description: "Zirui's innovative research in knowledge graph representation just earned a 2025 Research Excellence Award for 'AI & Machine Learning Applications.'",
      image: "/assets/kg_paradigm.png",
      link: "#"
    },
    {
      id: 2,
      category: "Research",
      title: "Knowledge Graph Representation of the Mappings between Seizure Semiology and Epileptogenic Zones",
      description: "Advanced research in epilepsy using knowledge graph representations to map seizure semiology and epileptogenic zones for improved clinical understanding.",
      image: "/assets/kg_paradigm.png",
      authors: "Shihao Yang, Zirui Wen, Wenxin Zhan, et al.",
      venue: "Under review at Epilepsia, 2025",
      link: "#"
    },
    {
      id: 3,
      category: "Conference",
      title: "COVID-19 Infection Prediction using Physical Signs",
      description: "Machine learning approach to predict COVID-19 infection using physical signs and symptoms, published in SPIE conference proceedings.",
      image: "/assets/1.png",
      authors: "Zirui Wen, Junjie Zhang, and Yuhao Zhang",
      venue: "CCPCDL 2022, SPIE Vol. 12287",
      link: "#"
    }
  ]

  return (
    <section className="publications">
      <div className="publications-hero">
        {publications.map((pub, index) => (
          <motion.div
            key={pub.id}
            className="publication-item"
            initial={{ opacity: 0, y: 50 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, delay: index * 0.2 }}
            viewport={{ once: true }}
          >
            <div className="publications-visual">
              <div className="hero-image-container">
                <img src={pub.image} alt={pub.title} className="hero-image" />
              </div>
            </div>
            
            <div className="publications-content">
              <div className="publication-category">{pub.category}</div>
              <h2 className="publication-main-title">{pub.title}</h2>
              <p className="publication-main-description">{pub.description}</p>
              {pub.authors && (
                <div className="publication-meta">
                  <span className="publication-authors">{pub.authors}</span>
                  <span className="publication-venue">{pub.venue}</span>
                </div>
              )}
              <a href={pub.link} className="publication-link">Learn More ↗</a>
            </div>
          </motion.div>
        ))}
      </div>
    </section>
  )
}

export default Publications
