import React from 'react'
import { motion } from 'framer-motion'
import './TechStack.css'

const TechStack = () => {
  const techCategories = [
    {
      title: "Languages",
      items: ["Python", "C++", "Java", "JavaScript", "HTML/CSS", "SQL"]
    },
    {
      title: "Deep Learning",
      items: ["PyTorch", "TensorFlow", "Keras", "scikit-learn", "Hugging Face", "LangChain", "vLLM", "MLFlow", "Optuna"]
    },
    {
      title: "Data Analysis",
      items: ["NumPy", "Pandas", "MySQL", "PostgreSQL", "MongoDB", "Redis", "Neo4j", "FAISS", "Feast", "Milvus", "Qdrant", "Weaviate"]
    },
    {
      title: "Web & DevOps",
      items: ["Flask", "FastAPI", "React", "Next.js", "Vue.js", "gRPC", "REST", "WebSockets", "Kafka", "Docker", "Kubernetes", "Helm"]
    },
    {
      title: "Cloud & Tools",
      items: ["Ray Serve", "KServe", "Triton", "Git", "Linux", "CUDA", "CI/CD", "AWS", "GCP", "Terraform", "GitHub Actions", "Apache Airflow", "Prometheus", "Grafana", "OpenTelemetry", "Schema Registry", "Azure", "Google Cloud"]
    }
  ]

  return (
    <section className="tech-stack">
      <div className="container">
        <div className="section-header centered">
          <h2 className="section-title">
            Technologies<span className="accent-dot">.</span>
          </h2>
        </div>
        
        <div className="tech-categories">
          {techCategories.map((category, categoryIndex) => (
            <motion.div
              key={category.title}
              className="tech-category"
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: categoryIndex * 0.1 }}
              viewport={{ once: true }}
            >
              <h3 className="tech-category-title">{category.title}</h3>
              <div className="tech-gallery">
                <div className="tech-scroll-container">
                  <div className="tech-scroll-track">
                    {[...category.items, ...category.items].map((item, index) => (
                      <motion.div
                        key={`${item}-${index}`}
                        className="tech-item"
                        whileHover={{ scale: 1.05, y: -2 }}
                        transition={{ duration: 0.2 }}
                      >
                        {item}
                      </motion.div>
                    ))}
                  </div>
                </div>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  )
}

export default TechStack
